import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent} from './styles.js'

class CodePage extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
    console.log(this.state.hover)
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
    console.log(this.state.hover)
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>

              <p>
                NOV 2020&nbsp;

                <StyledText onMouseOver={() => this.mouseOver('hover1')} onMouseOut={() => this.mouseOut('hover1')}>
                <Link to='/i2we/'>
                I2we
                </Link>
                </StyledText><br />

                AUG 2020&nbsp;

                <StyledText onMouseOver={() => this.mouseOver('hover2')} onMouseOut={() => this.mouseOut('hover2')}>
                <Link to='/yards/'>
                Yards
                </Link>
                </StyledText>
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default CodePage



